export const getRecentCities = (): string[] => {
  if (typeof window === "undefined") return [];

  const storedCities = sessionStorage.getItem("recentCities");
  return storedCities ? JSON.parse(storedCities) : [];
};

export const addToRecentCities = (city: string): string[] => {
  if (typeof window === "undefined") return [];

  // שליפת הערים מה-SessionStorage
  const storedCities = getRecentCities();

  // שימוש ב-Set כדי למנוע כפילויות
  const uniqueCities = new Set([city, ...storedCities]);

  // עדכון ה-SessionStorage
  const updatedCities = Array.from(uniqueCities);
  sessionStorage.setItem("recentCities", JSON.stringify(updatedCities));

  return updatedCities;
};
